import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/App/Layout"
import Navbar from "../components/App/Navbar"
import PageBanner from '../components/Common/PageBanner'
import BlogSidebar from '../components/Blog/BlogSidebar'
import Footer from "../components/App/Footer"
import {documentToReactComponents} from "@contentful/rich-text-react-renderer"
import {BLOCKS } from "@contentful/rich-text-types"

const ComponentName = ({ data }) => {
    const { blog: {
                title, 
                content : {
                    raw : blogContent
    }}} = data
    
    const options = {
        renderNode: {
            [BLOCKS.HEADING_1]: (node, children) => <h1>{children}</h1>,
            [BLOCKS.HEADING_2]: (node, children) => <h2>{children}</h2>,
            [BLOCKS.HEADING_3]: (node, children) => <h3>{children}</h3>,
            [BLOCKS.HEADING_4]: (node, children) => <h4>{children}</h4>,
            [BLOCKS.HEADING_5]: (node, children) => <h5>{children}</h5>,
            [BLOCKS.HEADING_6]: (node, children) => <h6>{children}</h6>,
            [BLOCKS.PARAGRAPH]: (node, children) => <p>{children}</p>,
            [BLOCKS.EMBEDDED_ASSET]: (node, children) => <img src={`https:${node.data.target.fields.file["en-US"].url}`} alt={children}/>,
        },
        renderMark: {},
    }
    const richText = documentToReactComponents(JSON.parse(blogContent),options)

    return (
        <Layout>
            <Navbar />
            
            <PageBanner
                pageTitle="Detalhes do Blog" 
                homePageText="Inicio" 
                homePageUrl="/" 
                activePageText={title}
            /> 

            <section className="blog-details-area ptb-100">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8 col-md-12">
                            <div className="blog-details-desc">
                                <p>{richText}</p>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-12">
                            <BlogSidebar />
                        </div>
                    </div>
                </div>
            </section>
            
            <Footer />
        </Layout>
    )
}

export const query = graphql`
query contentfulBlog($slug: String) {
    blog: contentfulBlog(slug: { eq: $slug }) {
        title
        content {
            raw
        }
    }
}
`

export default ComponentName
