import React from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'

const query = graphql`
    query {
        allContentfulBlog(limit: 6) {
            nodes {
                slug
                image {
                    file {
                        url
                    }
                }
                title
                date
                tag {
                    name
                }
            }
        }
    }
`

const BlogSidebar = () => {
    const data = useStaticQuery(query)
    
    const { allContentfulBlog: {
        nodes: blogs
    }} = data

    return (
        <div className="widget-area">
            <div className="widget widget_search">
                <h3 className="widget-title">Procura</h3>

                <form className="search-form">
                    <label>
                        <span className="screen-reader-text">Procura Por:</span>
                        <input type="search" className="search-field" placeholder="Procura..." />
                    </label>
                    <button type="submit">
                        <i className="bx bx-search-alt"></i>
                    </button>
                </form>
            </div>

            <div className="widget widget_tracer_posts_thumb">
                <h3 className="widget-title">Ãšltimos Posts</h3>
                { blogs.map((blog, idx) => {
                    return (
                        <article className="item" key={idx}>
                            <Link to={`/blogs/${blog.slug}`} className="thumb">
                                <img src={blog.image.file.url} alt="blog" />
                                <span className="fullimage cover" role="img"></span>
                            </Link>
                            <div className="info">
                                <span>{blog.date.substr(0,10)}</span>
                                <h4 className="title usmall">
                                    <Link to={`/blogs/${blog.slug}`}>
                                        {blog.title}
                                    </Link>
                                </h4>
                            </div>

                            <div className="tagcloud">
                            {blog.tag.map((t) => {
                                return (<div>{t.name}</div>)
                            })}
                            </div>

                            <div className="clear"/>
                        </article>
                    )})
                }
            </div>
        </div>
    )
}

export default BlogSidebar